body {
  height: 100%;
  font-size: 2rem;
  margin: 0;
  padding: 0;
  font-family: 'Josefin Sans';
  color: white;
  background: #FFA17F;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #00223E, #FFA17F);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, right top, left top, from(#00223E), to(#FFA17F));
  background: -webkit-linear-gradient(right, #00223E, #FFA17F);
  background: -o-linear-gradient(right, #00223E, #FFA17F);
  background: linear-gradient(to left, #00223E, #FFA17F); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

#logo {
  display: block;
  margin: 20px auto;
}

svg {
  display: block;
  padding: 20px;
  height: 256px;
  width: 256px;
  stroke-width: 1;
  margin-left: auto;
  margin-right: auto;
}

.title h1 {
  text-align: center;
  margin: 20px;
}

.astros {
  font-family: 'Open Sans';
/*  background-color:rgba(0,0,0,0.3);	border-radius: 10px;*/
  width: 60%;
  height: auto;
  margin-left: auto;
  margin-right:auto;
  padding: 20px;
  margin-bottom: 20px;
}

#count {
	text-align: center;
	margin: auto 100px;
}

#count-button {
  -webkit-transition-duration: 0.4s; /* Safari */
  -o-transition-duration: 0.2s;
     transition-duration: 0.2s;
  background-color: #ECECEC;
  color: darkslategray;
  display:block; /* change this from inline-block */
  width:40%; /* setting the width */
  padding: 20px 10px 60px 10px;
  margin:0 auto; /* this will center  it */
  border-width: 0;
  font-size: 2rem;
}

#count-button:hover {
  color: #ECECEC;
  background: #2C3E50;
  text-decoration: none;
}

ul {
  text-align: center;
  list-style: none;
}

li {
  border: 1px solid white;
  border-radius: 8px;
  width: auto;
  display: inline-block;
  margin: 5px 10px 5px 0;
  padding: 4px 10px;
  font-size: 3rem;
}

.container{
  min-height: 100%;
  position: relative;
}

.footer {
  position:absolute;
  bottom:0;
  width:100%;
  height:60px;   /* Height of the footer */
  text-align: center;
}



